@function catalogWidth($width, $margins) {
  @return 4 * $width +3 * $margins + 2*$catalog-padding;
}

@mixin starSize($size) {
  width: $size;
  height: $size;
}

@mixin background-property {
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}
